<template>
  <v-card color="secondary" flat dark>
    <v-card-title>
      <v-icon
        v-if="$route.name != 'DailyComplianceHome'"
        color="primary"
        class="pe-2"
        @click="handleClick()"
        >mdi-arrow-left-circle-outline</v-icon
      >
      <v-breadcrumbs :items="breadcrumbs" class="pa-0">
        <template v-slot:item="{ item }">
          <v-breadcrumbs-item
            :to="{ path: item.path }"
            :disabled="item.disabled"
            exact
          >
            <h1 v-if="item.small" class="text-body-1 primary--text">
              {{ item.title }}
            </h1>
            <h1 v-else class="text-h6 white--text">{{ item.title }}</h1>
          </v-breadcrumbs-item>
        </template>
      </v-breadcrumbs>
    </v-card-title>
    <v-card-text class="pt-4 pb-0">
      <router-view></router-view>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  components: {},
  props: {},
  created() {},
  computed: {
    breadcrumbs() {
      return this.$route.meta.breadcrumbs;
    },
  },
  data: () => ({}),
  methods: {
    handleClick() {
      const breadcrumbs = this.$route.meta.breadcrumbs;
      const prev = breadcrumbs[breadcrumbs.length - 2];
      this.$router.push({ path: prev.path });
    },
  },
};
</script>

<style scoped></style>
